@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body,html,* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  line-height: 1.6em;
}
.container {
  width: 80%;
  margin: 0 auto;
}
header {
 background:#06191f;  /* #074242; */
  color: #FFF;
  text-align: center;
  padding: 5px;
}
header h1 {
  margin-bottom: 5px;
}
header nav ul {
  list-style-type: none;
}
header nav ul li {
  display: inline-block;
  margin: 10px 10px;
}
header nav ul li a {
  text-decoration: none;
  color: #FFF;
}
header nav ul li a:hover {
  color: #C4C4C4;
}

/* SHOWCASE */
#showcase {
  background: url('https://wallpaperaccess.com/thumb/1267580.jpg') center no-repeat;
  background-size: cover;
  height: 550px;
  color: #FFF;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
#showcase .container h1 {
  font-size: 3rem;
}
/* NEWSLETTER */
#newsletter {
  padding: 15px 0;
   background:#2980B9;/* #074242; */
  color: #FFF;
  text-align: center;
}
#newsletter form {
  margin-top: 10px;
}
#newsletter form input {
  padding: 7px 3px;
  margin: 10px 0;
}
#newsletter form button {
  border: 0;
  outline: 0;
  color: #FFF;
  background:#2980B9; /*#002A2A;*/
  padding: 10px 15px;
}
#newsletter form button:hover {
  background: #568E8E;
}
/* SERVICES */
#services {
  text-align: center;
  padding-top: 20px;
  margin-top: 10px;
}
#services .box {
  margin: 30px 0;
  /* ONLY FOR MOBILE */
  padding-bottom: 20px;
  border-bottom: 1px #C4C4C4 solid;
}
#services .box:last-child{
  border-bottom: none;
}
#services .box img {
  width: 100px;
}
#services .box img:hover {
  opacity: .8;
}
/* FOOTER */
footer {
  background:#2980B9;   /*  #185B5B;*/
  text-align: center;
  color: #FFF;
  padding: 15px;
}

/* MEDIA QUERIES */
@media(min-width: 900px){
  /* HEADER */
  header::after {
      display: table;
      clear: both;
      content: '';
  }
  header #brand {
      float: left;
  }
  header nav {
      margin-top: 5px;
      float: right;
  }
  header nav ul li {
      margin-right: 70px;
  }
  /* SERVICES */
  #services {
      margin: 35px 0;
  }
  #services::after {
      display: table;
      clear: both;
      content: '';
  }
  #services .box {
      display: inline-block;
      width: 30%;
      padding: 10px;
      border-bottom: none;
      padding-bottom: 0;
  }
  /* NEWSLETTER */
  #newsletter::after {
      display: table;
      clear: both;
      content: '';
  }
  #newsletter .container {
      width: 60%;
  }
  #newsletter h2 {
      float: left;
      margin-top: 7px;
      font-size: 1.7rem;
  }
  #newsletter form input {
      padding: 7px 30px;
  }
  #newsletter form {
      float: right;
      margin-top: 0;
  }
}
@media(max-width:880px){
  header nav ul li {
      margin: 10px 10%;
  }
}
@media(max-width:500px){
  header nav ul li {
      margin: 10px 5%;
  }
}




 
 
/* typewriting animation */
/* // Fonts */

.typewriter__wrapper {
  display: flex; 
  justify-content: center;
}
  .typewriter__text {
    width: 0; 
    margin: 2rem auto 0 auto;
    text-align: center;
    white-space: nowrap; 
    border-right: 1px solid hsla(0, 100%, 100%, 0.75);
    overflow: hidden; 
  }


/* // Specific */
.line-1 {
  -webkit-animation: blink 1s step-end 4 backwards, 
  typewriter 1.5s 1.5s steps(25, end) forwards, 
  blink 1s 5s step-end 7, 
  remove-blink 0s 2.5s step-end forwards;
          animation: blink 1s step-end 4 backwards, 
  typewriter 1.5s 1.5s steps(25, end) forwards, 
  blink 1s 5s step-end 7, 
  remove-blink 0s 2.5s step-end forwards; 
}

.line-2 {
  -webkit-animation: remove-blink 0s 0s step-end forwards,
  typewriter 2s 3.5s steps(40, end) forwards,
  remove-blink 0s 3.5s forwards;
          animation: remove-blink 0s 0s step-end forwards,
  typewriter 2s 3.5s steps(40, end) forwards,
  remove-blink 0s 3.5s forwards;
}

.line-3 {
  -webkit-animation: remove-blink 0s 0s step-end forwards,
  typewriter 3s 5.5s steps(40, end) forwards,
  blink 1s 2.5s step-end infinite;
          animation: remove-blink 0s 0s step-end forwards,
  typewriter 3s 5.5s steps(40, end) forwards,
  blink 1s 2.5s step-end infinite;
}

/* // Animations */

@-webkit-keyframes typewriter { 
  0% {
    width: 0;
    border-right: 1px solid hsla(0, 100%, 100%, 0.75);
  }
  100% {
    width: 100%;
    border-right: 1px solid hsla(0, 100%, 100%, 0.75);
  }
}

@keyframes typewriter { 
  0% {
    width: 0;
    border-right: 1px solid hsla(0, 100%, 100%, 0.75);
  }
  100% {
    width: 100%;
    border-right: 1px solid hsla(0, 100%, 100%, 0.75);
  }
}

@-webkit-keyframes blink {
  0%,
  100% {
    border-color: transparent;
  }
  50% {
    border-color: hsla(0, 100%, 100%, 0.75);
  }
}

@keyframes blink {
  0%,
  100% {
    border-color: transparent;
  }
  50% {
    border-color: hsla(0, 100%, 100%, 0.75);
  }
}

@-webkit-keyframes remove-blink {
  to {
    border-color: transparent;
  }
}

@keyframes remove-blink {
  to {
    border-color: transparent;
  }
}

